*{
    margin:0px;
    padding:0px;
}

.Main{
    min-height:100vh;
}

.contactGrid{
    display:grid;
    grid-template-columns: repeat(2,1fr);
    justify-content:center;
    align-content:center;

    @media (max-width:600px){
        grid-template-columns: repeat(1,1fr);
    }
}

